@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Roboto:wght@100;300;400;500;700;900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-family: 'Roboto', sans-serif;
    background: rgba(55, 55, 55, 1);
}
.container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 4rem;
    padding: 1rem;
    margin: 1rem;
}
    .container .books{
    cursor: pointer;
    /* border: 1px solid black; */
    width: 140  px;
    height: 240 px;

}

.container img{
    width: 100%;
    /* height: 100%; 
    max-width: 200px; */
    /* overflow: hidden; */

}

.display{
    display: flex;
    padding: 2rem;
    /* gap: 1.5rem; */
    justify-content: space-between;
    align-items: center;
   
    ;
    color: #FFFFFF;

}
.display-img img{
    width: 202.88999807982887px;
    height: 299.21999928899527px;
    top: 193.38613891601562px;
    left: 49px;
    border-radius: 2px;
   transform: rotate(-5deg);

}
.restDetails{
    display: flex;
    flex-direction: column;
    gap: .5rem;
    padding: 1rem 3rem;
    background-color:  rgba(113, 196, 244, 0.87);
     border-radius: 10px;
     background : rgba(113, 197, 244, 0.38);
    flex-grow: 1;
}
.heading{
    display: flex;
    /* font-size: medium; */
    font-weight: bold;
    background : rgba(113, 196, 244, 0);
    
}

.authorDetails{
    display: flex;
    justify-content: space-between;
    color:rgba(255, 255, 255, 0.5);
    background: rgba(113, 196, 244, 0);
}
.authorDetails h3{
    font-weight: 300;
    background: rgba(113, 196, 244, 0);
}
.authorDetails p , .authorDetails span{
    background: rgba(113, 196, 244, 0);
}
.para{
    display: flex;
    font-family: Roboto;
    font-size: 12px;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: 0em;
    text-align: left;
    background: rgba(113, 196, 244, 0);
}
.para span{
    background: rgba(113, 196, 244, 0);
}
.Extra-info{
    display: flex;
    gap: 1.5rem;
    font-weight: 300;
    font-size: 12px;
    background: rgba(113, 196, 244, 0);
}
.Extra-info p , .Extra-info span{
    background: rgba(113, 196, 244, 0);
}


.btn{
    display: flex;
    align-self: self-end;
    gap: 1rem;
    background: rgba(113, 196, 244, 0);
}


.btn button{
    cursor: pointer;
    padding: .4rem 1rem;
    border-radius: 10px;
    border: none;
    background-color: rgba(255, 255, 255, 0.5);
}
.btn button:hover{
    background-color:  rgba(255, 255, 255, 0.993);
}
.navbar{
/* border: 2px solid rgba(195, 190, 190, 1); */
background-color: rgba(195, 190, 190, 1);
display: flex;
justify-content: space-evenly;
padding: 1rem 0;
background: rgba(55, 55, 55, 1);
}
.left{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
}
.middle{
    display: flex;
    align-items :center;
    justify-content: center;
    width: 50%;
}
.middle div{
    border: none;
}
.middle input{
  flex-grow: .5;
  background-color: black;
 height: 2rem;
 border: none;
 border-radius: 10px;
 padding: 1rem;
 color: wheat;
 outline: none;
}
.middle button{
 background-color: black;
 height: 2rem;
 border: none;
 border-radius: 10px;
 padding: 1rem;
 color: #FFFFFF;
 display: flex;
 justify-content: center;
 align-items: center;
 text-align: center;
 padding: 1rem;
 cursor: pointer;
 margin-left: 1rem;
}

.right{
    display: flex;
    align-items :center;
    justify-content: center;
    gap: 1rem;
}
.right img{
    width: 25px;
    cursor: pointer;
}
.logo img {
    width: 30px;
}

.textlogo img{
    width: 80px;
    
